html,
body {
	height: 100%;
	width: 100%;
}
body {
	margin: 0;
	padding: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
		"Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
		"Helvetica Neue", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}
.App,
#root {
	width: 100%;
	height: 100%;
}

.header {
	line-height: 55px;
	margin: 10px 0;
	padding-left: 15px;
	font-weight: lighter;
	border-left: 5px solid rgb(56, 117, 250);
	font-size: 2em;
	text-align: left;
}

.cui {
	/*danger*/
	--danger: rgb(230, 56, 56);
	--danger-text: white;
	--danger-hover: rgb(230, 76, 76);
	--danger-text-hover: white;

	/*warning*/
	--warning: rgb(251, 201, 62);
	--warning-text: black;
	--warning-hover: rgb(255, 221, 82);
	--warning-text-hover: black;

	/* success */
	--success: rgb(86, 200, 86);
	--success-text: black;
	--success-hover: rgb(106, 230, 106);
	--success-text-hover: black;

	/* info */
	--info: rgb(35, 105, 240);
	--info-text: white;
	--info-hover: rgb(50, 117, 250);
	--info-text-hover: white;

	/* default */
	--default: #f1f1f1;
	--default-text: #333;
	--default-hover: #fafafa;
	--default-text-hover: #333;

	box-sizing: border-box;
}

.padded-container {
	width: 100%;
	height: 100%;
	box-sizing: border-box;
	padding: 10px;
}
.padded-container.horizontal {
	padding: 0 10px;
}
.padded-container.vertical {
	padding: 10px 0;
}
a:focus {
	text-decoration: none;
	outline: none;
}

.btn {
	--shadow-color: rgba(100, 100, 100, 0.4);
	--shadow-color-hover: rgba(100, 100, 100, 0.3);
	--shadow: 0 4px 4px var(--shadow-color);
	--shadow-hover: 0 2px 4px var(--shadow-color-hover);
	--btn-color: var(--default);
	--btn-text-color: var(--default-text);
	--btn-hover-color: var(--default-hover);
	--btn-text-hover-color: var(--default-text-hover);
	background: var(--btn-color);
	color: var(--btn-text-color);
	border: 0;
	text-align: center;
	min-width: 75px;
	line-height: 40px;
	margin: 4px 5px;
	outline: 0;
	padding: 2px 15px;
	transition: 0.3s;
	cursor: pointer;
	box-shadow: var(--shadow);
	border-radius: 3px;
	display: inline-block;
	text-transform: capitalize;
	font-size: 0.95em;
}
.btn-icon {
	text-align: center;
	--icon-height: 33px;
	font-size: 1.3em;
	width: var(--icon-height);
	line-height: var(--icon-height);
	height: var(--icon-height);
	min-width: 0;
	box-shadow: none;
	padding: 0;
	--btn-color: var(--default-hover);
	--btn-text-color: #888;
	--btn-hover-color: #dedede;
	--btn-text-hover-color: #000;
	--shadow: 0 4px 4px transparent;
	--shadow-hover: 0 2px 4px transparent;
}
.btn-icon i {
	font-size: inherit;
	line-height: var(--icon-height);
}
.btn-rounded {
	border-radius: 50%;
}
.btn-small,
.btn-smaller {
	line-height: 35px;
	font-size: 0.98em;
}
.btn-smaller {
	margin: 0;
	padding: 0;
}
.btn:hover,
.btn:active,
.btn:focus,
.btn.active {
	box-shadow: var(--shadow-hover);
	--btn-color: var(--btn-hover-color);
	--btn-text-color: var(--btn-text-hover-color);
}
.btn-danger {
	--btn-color: var(--danger);
	--btn-text-color: var(--danger-text);
	--btn-hover-color: var(--danger-hover);
	--btn-text-hover-color: var(--danger-text-hover);
	--shadow-color: rgba(255, 0, 0, 0.4);
	--shadow-color-hover: rgba(255, 0, 0, 0.3);
}
.btn-flat.btn-danger {
	--btn-text-color: var(--danger);
	--btn-text-hover-color: var(--danger);
}
.btn-warning {
	--btn-color: var(--warning);
	--btn-text-color: var(--warning-text);
	--btn-hover-color: var(--warning-hover);
	--btn-text-hover-color: var(--warning-text-hover);
	--shadow-color: rgba(201, 151, 12, 0.4);
	--shadow-color-hover: rgba(201, 151, 12, 0.3);
}
.btn-flat.btn-warning {
	--btn-text-color: rgb(255, 136, 0);
	--btn-text-hover-color: orange;
	/* text-shadow: 0 0 15px rgba(0, 0, 0, 0.1); */
}
.btn-success {
	--btn-color: var(--success);
	--btn-text-color: var(--success-text);
	--btn-hover-color: var(--success-hover);
	--btn-text-hover-color: var(--success-text-hover);
	--shadow-color: rgba(56, 170, 56, 0.4);
	--shadow-color-hover: rgba(56, 170, 56, 0.3);
}
.btn-flat.btn-success {
	--btn-text-color: var(--success);
	--btn-text-hover-color: var(--success);
}

.btn-info {
	--btn-color: var(--info);
	--btn-text-color: var(--info-text);
	--btn-hover-color: var(--info-hover);
	--btn-text-hover-color: var(--info-text-hover);
	--shadow-color: rgba(0, 104, 184, 0.4);
	--shadow-color-hover: rgba(0, 104, 184, 0.3);
}
.btn-flat.btn-info {
	--btn-text-color: var(--info);
	--btn-text-hover-color: var(--info);
}

.btn-flat {
	font-weight: bold;
	--btn-color: transparent;
	--btn-hover-color: rgba(80, 80, 80, 0.1);
	--shadow-color: transparent;
	--shadow-color-hover: rgba(0, 0, 0, 0);
	border-radius: 5px;
}
.btn:disabled {
	cursor: not-allowed;
	--shadow-color: transparent;
	--shadow-color-hover: transparent;
	--btn-color: #ddd;
	--btn-text-color: #333;
	font-weight: normal;
}

.custom-input {
	--border-color: #fff;
	--shadow-color: rgba(0, 0, 0, 0.1);
	--error-shadow: rgba(255, 106, 106, 0.2);
	--error: rgb(255, 106, 106);
	position: relative;
	display: block;
	box-shadow: 0 3px 10px var(--shadow-color);
	border: 1px solid var(--border-color);
	transition: 0.3s;
	border-radius: 3px;
	margin: 25px 0;
	text-align: left;
}
.custom-input.error {
	--shadow-color: var(--error-shadow);
}
.custom-input:hover {
	box-shadow: 0 2px 5px var(--shadow-color);
}
.custom-input .label {
	width: 100%;
	height: 100%;
	position: absolute;
	height: 45px;
	line-height: 45px;
	padding-left: 10px;
	color: #999;
	font-weight: lighter;
	box-sizing: border-box;
	top: 0;
	left: 0;
	transition: 0.2s;
	cursor: text;
	border-left: 1px solid #fff;
	border-right: 1px solid #fff;
}
.custom-input .label::selection {
	background: transparent;
}
.custom-input input {
	border: none;
	outline: none;
	width: 100%;
	height: 45px;
	line-height: 45px;
	padding: 10px;
	box-sizing: border-box;
	font-size: 17px;
	background: transparent;
}

.custom-input input:focus + .label,
.custom-input input:not([value=""]) + .label {
	top: -6px;
	left: 5px;
	font-size: 0.7em;
	color: #000;
	background: #fff;
	width: auto;
	height: 12px;
	line-height: 12px;
	padding: 0 4px;
}
.custom-input.error {
	--border-color: var(--error);
}
.custom-input.error input:focus + .label,
.custom-input.error input:not([value=""]) + .label {
	border-left: 1px solid var(--error);
	border-right: 1px solid var(--error);
}
.custom-input.error .label {
	color: var(--error);
}
.custom-input .error-message {
	color: rgb(250, 56, 56);
	opacity: 0;
	font-size: 10px;
	position: absolute;
	margin-top: -5px;
	margin-left: 6px;
	background: #fff;
	padding: 0 4px;
	border-left: 1px solid var(--error);
	border-right: 1px solid var(--error);
	transition: 0.3s;
}
.custom-input.error .error-message {
	opacity: 1;
}

.modal-backdrop {
	background: rgba(0, 0, 0, 0.5);
	position: fixed;
	top: 0;
	left: 0;
	height: 100%;
	width: 100%;
	z-index: 5;
}
.modal-body {
	position: absolute;
	top: 50%;
	min-width: 500px;
	width: 60%;
	left: 50%;
	transform: translate(-50%, -50%);
	background: white;
	box-sizing: border-box;
	border-radius: 5px;
	box-shadow: 0 10px 25px rgba(0, 0, 0, 0.3);
	animation: 0.3s drop 1;
	overflow: hidden;
}
.modal-body.modal-small {
	width: 45%;
	min-width: 300px;
}
.modal-body.modal-medium {
	width: 60%;
	min-width: 500px;
}
.modal-body.modal-large {
	width: 80%;
	min-width: 500px;
}
.modal-body.modal-massive {
	width: 90%;
	min-width: 600px;
}
.modal-body.modal-full {
	width: 100%;
	height: 100%;
	border-radius: 0;
}
@keyframes drop {
	0% {
		transform: translate(-50%, -50%) scale(0.5);
	}

	100% {
		transform: translate(-50%, -50%);
	}
}
.modal-header {
	font-size: 25px;
	padding: 20px 30px;
	padding-bottom: 15px;
}
.modal-content {
	padding: 0px 0px;
	padding-bottom: 0;
	font-size: 14px;
	color: #555;
}

.centered {
	width: 300px;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	padding: 20px;
	text-align: center;
	/* box-shadow: 0 0px 15px rgba(0, 0, 0, 0.1); */
}

.sidebar {
	width: 50px;
	height: 100%;
	background: #fff;
	box-sizing: border-box;
	border-right: 1px solid #eee;
	/* box-shadow: 3px 0 5px rgba(0, 0, 0, 0.1); */
}
.sidebar .list-item {
	text-decoration: none;
	display: list-item;
	width: 100%;
	line-height: 50px;
	height: 50px;
	list-style-type: none;
	text-align: center;
	color: rgb(11, 107, 216);
	cursor: pointer;
	transition: 0.3s;
}
.sidebar .list-item i {
	line-height: 50px;
	font-size: 1.2em;
}
.sidebar .list-item:hover {
	background: #eee;
	/* color: white; */
}

.dashboard-container {
	display: flex;
	width: 100%;
	height: 100%;
}

.editor-sidebar {
	width: auto;
	height: 100%;
	display: flex;
}
.sidebar-headers {
	width: 50px;
	text-align: center;
}
.editor-sidebar-tab {
	width: 100%;
	height: 50px;
	line-height: 50px;
	background: #111;
	list-style-type: none;
	color: gold;
	cursor: pointer;
}
.editor-sidebar-tab:hover,
.editor-sidebar-tab.active {
	background: #333;
}
.editor-sidebar.active .sidebar-tab-body {
	display: block;
}
.sidebar-tab-body {
	background: #333;
	min-width: 300px;
	display: none;
}

.editor-component {
	display: flex;
	width: 100%;
	height: 100%;
	background: #111;
	color: #fff;
}

